import './Activities.css'
import Dialog from "@material-ui/core/Dialog";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import Button from "@material-ui/core/Button";
import React, {useState} from 'react';

const Activities = (props)=> {

  const {buttonPopup, setButtonPopup} = useState(false);

  const [open, setOpen] = React.useState(false);
  const handleClickToOpen = () => {
    console.log(props)
    setOpen(true);
  };

  const handleToClose = () => {
    setOpen(false);
  };

  return(
    <div style={{
      font: 'Helvetica',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      }}>

    <Button variant="outlined" color="primary" onClick={handleClickToOpen}>
        Activities </Button>

      <Dialog open={open} onClose={handleToClose}>
        <DialogTitle>{"Lists of Parks Activities"}</DialogTitle>
        <DialogContent> 
            <p> The following hyperlinks will direct you to one of many National Parks with these corresponding activitites. </p>

            {props.data ? props.data.map((x) => {
              return <h1 key={x}> <a href={x.parks[0].url}> {x.name}  </a>  </h1>
            }) : null} 
        </DialogContent>

        <DialogActions>
          <Button onClick={handleToClose} 
                  color="primary" autoFocus> Close
          </Button>
        </DialogActions>
      </Dialog>
      </div>
  )
}
export default Activities