import './Webcams.css'
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import Button from "@material-ui/core/Button";
import React, {useState} from 'react';

const Webcams = (props)=> {

  const {buttonPopup, setButtonPopup} = useState(false);

  const [open, setOpen] = React.useState(false);
  const handleClickToOpen = () => {
    console.log(props)
    setOpen(true);
  };

  const handleToClose = () => {
    setOpen(false);
  };

  return(
    <div style={{
        font: 'Helvetica',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      }}>

    <Button variant="outlined" color="primary" onClick={handleClickToOpen}>
        Webcameras </Button>

      <Dialog open={open} onClose={handleToClose}>
        <DialogTitle>{"Links to Live Park Webcameras"}</DialogTitle>
        <DialogContent>
            {props.data2 ? props.data2.map((x) => {
              return <h1 key={x}> <a href= {x.url}> {x.title} </a> </h1>
            }) : null}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleToClose} 
                  color="primary" autoFocus> Close
          </Button>
        </DialogActions>
      </Dialog>
      </div>
  )
}
export default Webcams